	import {api} from '../../api/api.js'
	import ListItem from './child/ListItem.vue'
	export default {
		data() {
			return {
				windowWidth: getApp().globalData.windowWidth,
				windowHeight: getApp().globalData.windowHeight,
				top: getApp().globalData.top,
				session_id:getApp().globalData.session_id,
				uid:getApp().globalData.uid,
				list: []/* {
						grade: 0,
						num: 0
					},
					{
						grade: 1,
						num: 1
					},
					{
						grade: 2,
						num: 12
					},
					{
						grade: 3,
						num: 32
					},
					{
						grade: 4,
						num: 142
					},
					{
						grade: 5,
						num: 32
					},
					{
						grade: 6,
						num: 12
					},
					{
						grade: 7,
						num: 18
					}
 */
				
			}
		},
		onLoad() {
			this.getList();
		},
		methods: {
			toBack() {
				uni.navigateBack();
			},
			getList() {
				uni.request({
					url:api.team,
					data: {
						uid:this.uid,
						session_id:this.session_id
					},
					method:"post",
					header: {
						"Content-Type": "application/x-www-form-urlencoded"
					},
					success: (res) => {
						console.log(res);
						if(res.data.code === "success") {
							this.list = res.data.result;
						}else {
							uni.showToast({
								title:res.data.msg,
								duration:1500,
								icon:"none"
							})
						}
					}
				})
			}
		},
		components: {
			ListItem
		}
	}
